@import '../../variables';

.home-page {
  height: fit-content;
  .text-zone {
  position: absolute;
  left: 10%;
  top: 50%;
  transform: translateY(-50%);
  width: 40%;
  max-height: 90%;
  }

  h1{
    color: #fff;
    font-size: 53px;
    margin: 0;
    font-family: 'Coolvetica';
    font-weight: 400;

    img {
      width: 72px;
      margin-left: 20px;
      margin-right: 5px;
      margin-bottom: -30px;
      opacity: 0;
      height: auto;
      animation:  rotateIn 1s ease-out both;
      animation-delay: 1.4s;
    }
  }

  h2 {
    color: $secondary-color;
    margin-top: 20px;
    font-weight: 400;
    font-size: 11px;
    font-family: sans-serif;
    letter-spacing: 3px;
    animation: fadeIn 1s 1.8s backwards;

  }

  .flat-button {
    color: $primary-color;
    font-size: 13px;
    font-weight: 400;
    letter-spacing: 4px;
    font-family: sans-serif;
    text-decoration: none;
    padding: 10px 18px;
    border: 1px solid $primary-color;
    margin-top: 25px;
    float: left;
    animation: fadeIn 2s 1.8s backwards;
    white-space: nowrap;

    &:hover {
      background: $primary-color;
      color: $button-text;
    }
  }

}

@media only screen and (max-width: 500px) {
  .home-page {
    height: fit-content;
    .text-zone {
    position: absolute;
    left: 3%;
    top: 50%;
    transform: translateY(-50%);
    width: 80%;
    max-height: 90%;
    }

    ._18,
    ._30
    {
      margin-right: -3.5px;
      margin-left: 1px;
    }
  
    h1{
      color: #fff;
      font-size: 29px;
      margin: 0;
      font-family: 'Coolvetica';
      font-weight: 400;
  
      img {
        width: 47px;
        margin-left: 20px;
        margin-right: 5px;
        margin-bottom: -20px;
        opacity: 0;
        height: auto;
        animation:  rotateIn 1s ease-out both;
        animation-delay: 1.4s;
      }
    }
  }

}